<!-- *************************************************************************
     TEMPLATE
     ************************************************************************* -->

<template lang="pug">
 .c-instagram-stat
  span.c-instagram-stat__value {{ formattedValue }}

  span.c-instagram-stat__type {{ type }}
</template>

<!-- *************************************************************************
     SCRIPT
     ************************************************************************* -->

<script>
// NPM
import numeral from "numeral"

export default {
  props: {
    abbreviate: {
      type: Boolean,
      default: false
    },
    separator: {
      type: Boolean,
      default: false
    },
    type: {
      type: String,
      required: true
    },
    value: {
      type: Number,
      required: true
    }
  },

  computed: {
    formattedValue() {
      if (this.abbreviate && this.value >= 1000) {
        return numeral(this.value).format("0.0a")
      } else if (this.separator) {
        return numeral(this.value).format("0,0")
      }

      return this.value
    }
  }
}
</script>

