  #{$c}__type {
    display: block;
  }

  #{$c}__value {
    margin-bottom: 3px;
    font-weight: 600;
    font-size: 13px;
    line-height: 13px;
  }

  #{$c}__type {
    text-transform: capitalize;
    font-size: 12px;
